	module.exports = {"data":{"markdownRemark":{"html":"<p>In this episode, I respond to a listener request for more talk about creating effective, interesting animated video. I could have gone high-tech, but instead, I challenge you to think about using PowerPoint for effective animated video.</p>\n<iframe src=\"https://simplecast.com/e/49390?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>Serendipitously, this episode and the next one (stay tuned!) coincide with the release of Articulate 360. If you’re looking for alternative tools to make stuff, give PowerPoint for animated video a go! Do you have any unconventional video tips or any examples of creating interactive/branched video. I’d love to hear from you. Leave it in the comments! As always, you can get the show notes on the show page: <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a></p>","frontmatter":{"date":"November 24, 2016","path":"/blog/dearid-powerpoint-for-video","title":"DearID: PowerPoint for Video","tags":["animation","maker challenge","PowerPoint","tools","video"],"categories":["Dear ID Show"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1,"src":"/static/PowerPoint-as-a-Video-Tool-6935bf9189eee247950c4da46f549f9d-81d39.png","srcSet":"/static/PowerPoint-as-a-Video-Tool-6935bf9189eee247950c4da46f549f9d-9a8d1.png 203w,\n/static/PowerPoint-as-a-Video-Tool-6935bf9189eee247950c4da46f549f9d-c53f9.png 405w,\n/static/PowerPoint-as-a-Video-Tool-6935bf9189eee247950c4da46f549f9d-81d39.png 810w,\n/static/PowerPoint-as-a-Video-Tool-6935bf9189eee247950c4da46f549f9d-e460a.png 1215w,\n/static/PowerPoint-as-a-Video-Tool-6935bf9189eee247950c4da46f549f9d-f34da.png 1400w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

